.RoundSelect {
  width: 190px;
}

.RoundSelect .Choice {
  width: 100%;
  margin: 10px;
  padding: 10px;
  border-radius: 20px;
  cursor: pointer;
  background-color: black;
  color: white;
  opacity: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.RoundSelect .Choice:hover {
  background-color: gray;
}

.RoundSelect .Choice.first {
  background-color: black;
  color: white;
  opacity: 1;
}


